html {
	min-height: 100%;

	/*	height: 100%;*/
	position: relative;
}
body {
	margin: 0;

	color: rgba(0, 0, 0, 0.87);
	font-family: "Roboto", "sans-serif";
	background: #96190e;
}

#root {
}
h1 {
	line-height: 40px !important;
	height: 40px !important;
	font-size: 1.2em !important;
}
.search-wrapper {
	text-align: center;
	margin-top: 2em;
	/*margin-bottom: 80px;*/
	/*	height: 100%;*/
}

.login-btn {
	margin-top: 15em !important;
}

.gallery {
	max-width: 960px;
	margin: auto;
	margin-bottom: 50px;
	position: relative;
}

.profile {
	width: 90%;
	max-width: 960px;
	margin: 20px auto 50px auto;
}

.bottom-nav > div > div {
	color: #fff !important;
}

.track {
	width: 200px;
	display: inline-block;
	margin: auto 20px 50px 20px;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
	.track {
		margin: auto auto 50px auto;
		display: block;
		width: 256px;
	}
	.profile {
		width: 256px;
		margin: 20px auto 50px auto;
	}
}



